.accordion {
  display: flex;
  flex-direction: column;
  background: rgba(21, 18, 58, 0.5);
  border: 1px solid #ffffff;
  box-sizing: border-box;
  transition: height 1s ease-in-out;
  min-height: 80px;
  padding: 20px;
  width: 45%;
  &__title {
    margin: 0;
  }
  &__btn {
    background: none;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: none;
    outline: none;
    transition: background-color 0.6s ease;
    cursor: pointer;
    padding: 0;
  }
  &__btn:hover {
    opacity: 0.7;
  }

  &__icon {
    transition: transform 0.6s ease;
  }
  &__content {
    overflow: hidden;
    transition: max-height 0.6s ease;
  }
}
